import React, { Component } from "react";
import { Form, Button} from "react-bootstrap";
import OtherAuthAction from "./Components/OtherAuthAction";
import { FontAwesomeIcon } from "../../Assets/AssetsHelper";
import { resetPasswords } from '../../actions/loginActions'
import { validatePassword } from "../../utils";
import PwdStrength from "../Layout/Main/CommonComponents/PwdStrength";
import withRouter from "../routes/WithRouter";
import FormTerms from "./Components/FormTerms";

class ResetPassword extends Component {
    
    state = { accountPassword: null, passwordError: null, accountRePassword: null, passwordReError: null, success: false, strength: 0 , btnLoader:false, visiblePass:false, reVisiblePass:false, successError:false , strongPass:true}

    onPasswordReTextChange = event => {
        this.setState({ accountRePassword: event.target.value }, () => {
            if (this.state.accountPassword != this.state.accountRePassword) this.setState({ passwordReError: true })
            else this.setState({ passwordReError: false })
        })
    }

    onClickToResetPassword = event => {
        const { accountPassword, accountRePassword, passwordError, passwordReError, strength } = this.state;
        const { token, userId } = this.props.params;
        if(strength == 4){
        var self = this;
        self.setState({ btnLoader:true })
        if(accountPassword && String(accountPassword).length >= 8 && accountRePassword && String(accountRePassword).length == String(accountPassword).length && !passwordError && !passwordReError){
            resetPasswords({ userId: userId, token: token, password: accountPassword }).then((response) => {
                this.props.navigate('/accounts/login?reset=1')
            }).catch(()=>{
                this.props.navigate('/accounts/login?reset=2')
            })
        }
        else{
            if(!accountPassword || String(accountPassword).length < 8) this.setState({passwordError: true})
            else if(accountPassword != accountRePassword) this.setState({ passwordReError: true})
            this.setState({btnLoader:false})
        }
      }
      else{
            this.setState({strongPass:false})
        }
    }


    checkPassStrength = event => {
        const password = event.target.value;
        const { strength } = validatePassword(password); 
        this.setState({ accountPassword: password, strength, passwordReError: null, passwordError: false , strongPass: strength==4 ? true :this.state.strongPass});
    }

    showPassword = () => {
        this.setState({
            visiblePass: !this.state.visiblePass
        })
    }
    render() {
        const { prefix, params } = this.props
        const { strength, passwordError, success, accountRePassword, passwordReError, accountPassword, btnLoader, visiblePass, reVisiblePass, successError, strongPass} = this.state

        return <div className="w-100 mb-lg-0 mb-3">
                    <div className="text-start mb-5">
                        <h1 className=" mb-0 display-2 fw-bold mb-3">Reset Password?</h1>
                    </div>
                    {success ? <div className={`alert alert-${successError ? `danger` :`success`} py-2`} role="alert">
                        {successError ? `Some error occured, please try again later.` : `You have successfully updated your password. You can login now with your new password.`}
                    </div> : null}

                    {!success ? <> 
                    <div className="mb-8">
                        <Form.Group className="mb-4 mb-xl-5">
                            <Form.Label className="fw-bold " htmlFor="password_">Enter Password</Form.Label>
                            <span className={`position-relative d-block mb-2 ${passwordError ? 'is-invalid':''}`}>
                                <Form.Control type={visiblePass ? 'text' : 'password'} id="password_" size="lg" placeholder="Password" minLength="8" maxLength="24" onChange={this.checkPassStrength} value={accountPassword} />
                                <span className="view_pass fs-6 btn btn-icon bg-transparent border-0 text-gray-500 position-absolute translate-middle top-50" onClick={() => this.setState({ visiblePass: !visiblePass })}>
                                    <FontAwesomeIcon icon={visiblePass ? 'eye' : 'eye-slash'}/>
                                </span>
                            </span>
                            {passwordError ? <span className="invalid-feedback mb-1">Enter valid password.</span> : null}
                            <span className={`${strongPass ? 'text-muted' :'text-danger'} fs-9 mb-2`}>Password must be at least 8 characters long and include a combination of letters, numbers, and special symbols (e.g., @, #, $, %).</span>
                        </Form.Group>
                        <PwdStrength strength={strength} />
                        <div className="fv-row mb-3 pt-1">
                            <Form.Group className={`mb-4 mb-xl-5`}>
                                <Form.Label className="fw-bold " htmlFor="re-password_">Repeat Password</Form.Label>
                                <span className={`position-relative d-block mb-2  ${passwordReError ? `is-invalid` : ``} `}>
                                    <Form.Control type={reVisiblePass ? 'text' : 'password'} id="re-password_" size="lg" placeholder="Repeat Password" minLength="8" maxLength="24" onChange={this.onPasswordReTextChange} value={accountRePassword || ''} />
                                    <span className="view_pass fs-6 btn btn-icon bg-transparent border-0 text-gray-500 position-absolute translate-middle top-50" onClick={() => this.setState({ reVisiblePass: !reVisiblePass })}>
                                        <FontAwesomeIcon icon={reVisiblePass ? 'eye' : 'eye-slash'}/>
                                    </span>
                                </span>
                                {passwordReError ? <div className="invalid-feedback">Password does not match</div> : null}
                            </Form.Group>
                        </div>
                    </div>
                    <div className="d-grid mb-3">
                        <Button type="submit" variant="primary" size={'lg'} className={`${btnLoader ? ' disabled' : ''}`} onClick={this.onClickToResetPassword}>
                            <span className="indicator-label" style={{ display: btnLoader ? 'none' : 'block' }}>Reset Password</span>
                            <span className="indicator-progress" style={{ display: !btnLoader ? 'none' : 'block' }}> Please wait...
                                <span className="spinner-border spinner-border-sm align-middle ms-2"></span>
                            </span>
                        </Button>
                    </div>
                </> : null}
                <OtherAuthAction text="Back to" link={`${prefix}accounts/login`} linkText={'Login'} />
                <FormTerms/>
            </div>
    }
}

export default withRouter(ResetPassword)