import { Link } from 'react-router-dom';
const OtherAuthAction = ({ text, link, linkText }) => <div className=" text-center fw-normal fs-8 mt-lg-5 mt-1 f-center mb-5">{text}
       <Link to={link} className="btn btn-link text-decoration-none fw-bold fs-8 p-0 ms-1" title="Auth">{linkText}</Link>
</div>
export default OtherAuthAction;
